	module.exports = {"data":{"markdownRemark":{"html":"<h2>Description</h2>\n<p> Opt-in using the pragma <code>\"use strict\";</code></p>\n<p>Applied to the top of a function makes the whole function run in strict mode.\nApplied to the top of a script makes the whole script run in strict mode.\nes6 modules run  implicitly in strict mode.</p>\n<table>\n<thead>\n<tr>\n<th></th>\n<th>Sloppy Mode</th>\n<th>Strict Mode</th>\n</tr>\n</thead>\n<tbody>\n<tr>\n<td><em>this default binding</em></td>\n<td>global Object</td>\n<td>undefined</td>\n</tr>\n<tr>\n<td><em>accidental global variable</em>\n \n<code>cart = 42;</code></td>\n<td>applies property to global object</td>\n<td>throws Reference error</td>\n</tr>\n<tr>\n<td><em>delete read-only prop</em></td>\n<td>fails silently</td>\n<td>throws TypeError</td>\n</tr>\n<tr>\n<td><em>add prop to a non-extendible object</em></td>\n<td>fails silently</td>\n<td>throws TypeError</td>\n</tr>\n<tr>\n<td><em>change property descriptor if configurable is false</em></td>\n<td>fails silently</td>\n<td>throws TypeError</td>\n</tr>\n<tr>\n<td><em>with</em></td>\n<td>works normally</td>\n<td>forbidden throws Error</td>\n</tr>\n<tr>\n<td><em>octal notation:</em>\n \n<code>let a = 05;</code></td>\n<td>works</td>\n<td>forbidden throws SyntaxError only \n<code>let a = 0o5;</code>\n works</td>\n</tr>\n<tr>\n<td><em>assign properties to primitives</em>\n \n<code>(2).a = 3; window.undefined = true; \"a\".a = \"a\";</code></td>\n<td>fails silently</td>\n<td>forbidden throws TypeError</td>\n</tr>\n<tr>\n<td><em>eval and arguments used as variable/property names</em></td>\n<td>works</td>\n<td>throws TypeError</td>\n</tr>\n<tr>\n<td><em>delete plainNames</em>\n \n<code>delete undesired;</code></td>\n<td>works</td>\n<td>throws SyntaxError</td>\n</tr>\n<tr>\n<td><em>addings variables to scope using eval</em>\n \n<code>eval(\"var r = 3;\")</code></td>\n<td>adds variable to scope</td>\n<td>doesnt not add a variable</td>\n</tr>\n</tbody>\n</table>\n<h2>References</h2>\n<ul>\n<li><a href=\"https://developer.mozilla.org/en-US/docs/Web/JavaScript/Reference/Strict_mode\">https://developer.mozilla.org/en-US/docs/Web/JavaScript/Reference/Strict_mode</a></li>\n<li><a href=\"https://github.com/getify/You-Dont-Know-JS/blob/1efb94d9a98d873461e3416de625e398ddce31fd/scope%20%26%20closures/ch2.md\">https://github.com/getify/You-Dont-Know-JS/blob/1efb94d9a98d873461e3416de625e398ddce31fd/scope%20%26%20closures/ch2.md</a></li>\n<li><a href=\"https://github.com/getify/You-Dont-Know-JS/blob/31e1d4ff600d88cc2ce243903ab8a3a9d15cce15/this%20%26%20object%20prototypes/ch2.md\">https://github.com/getify/You-Dont-Know-JS/blob/31e1d4ff600d88cc2ce243903ab8a3a9d15cce15/this%20%26%20object%20prototypes/ch2.md</a></li>\n</ul>","frontmatter":{"date":"May 27, 2018","path":"/p/js/strict-mode","title":"Strict Mode","section":"js"}}},"pathContext":{}}

